






import Vue from 'vue'
import { Component, Prop } from 'vue-property-decorator';
import { obtenerJson, sleep } from '../tools/tools';

import Chart, {ChartData, ChartDataSets, ChartConfiguration, ChartOptions} from 'charts.vue2';
import { CasosDiarios } from '../model/geo';
import { fixFechas, MyDataset } from '@/tools/graphs_tools';

@Component({
  components: {Chart}
})
export default class GraficoCasosDiarios extends Vue {
  @Prop() store: any;
  tipoGrafico : string = 'line';
  fechasCasos : string[] = [];

  labels = ['Lunes', 'Martes', 'Miércoles', 'Jueves', 'Viernes', 'Sábado', 'Domingo'];
  dataset : ChartDataSets[] = [{
    label: 'Casos por día',
    backgroundColor: '#f87979',
    data: [1,2,3]
  }];

  options: ChartOptions = {
    responsive: true,
    maintainAspectRatio: false,
    scales : {
      yAxes : [{ticks:{min: 0}}]
    },
    title: {text:'Casos activos aproximados (15 días)',position:'top',display:true,fontSize:20},
    legend: {position:'bottom'},
    layout: {
      
    }
  }

  async init(){
    this.fechasCasos = this.store.fechasCasos; //await obtenerJson("/fechas_casos");

    //const casos_diarios = await this.obtenerCasosDiarios(this.fechasCasos[this.fechasCasos.length - 1],'reload');

    const casosPromedioSS  = await obtenerJson('/casos_diarios/*/374',{method:'get', mode: 'cors', cache: 'reload'}) as Array<CasosDiarios>;
    const casosPromedioSoy = await obtenerJson('/casos_diarios/*/377',{method:'get', mode: 'cors', cache: 'reload'}) as Array<CasosDiarios>;
    const casosPromedioSM = await obtenerJson('/casos_diarios/*/479',{method:'get', mode: 'cors', cache: 'reload'}) as Array<CasosDiarios>;
    const casosPromedioSA = await obtenerJson('/casos_diarios/*/286',{method:'get', mode: 'cors', cache: 'reload'}) as Array<CasosDiarios>;
    const casosPromedioST = await obtenerJson('/casos_diarios/*/349',{method:'get', mode: 'cors', cache: 'reload'}) as Array<CasosDiarios>;
    const casosPromedioCo = await obtenerJson('/casos_diarios/*/341',{method:'get', mode: 'cors', cache: 'reload'}) as Array<CasosDiarios>;
    if(casosPromedioSS == undefined) return;

    casosPromedioSS.sort((a,b) => a.fecha  < b.fecha ? -1 : 1)
    casosPromedioSoy.sort((a,b) => a.fecha < b.fecha ? -1 : 1)
    casosPromedioSM.sort((a,b) => a.fecha < b.fecha ? -1 : 1)
    casosPromedioSA.sort((a,b) => a.fecha < b.fecha ? -1 : 1)
    casosPromedioST.sort((a,b) => a.fecha < b.fecha ? -1 : 1)
    casosPromedioCo.sort((a,b) => a.fecha < b.fecha ? -1 : 1)

    this.labels = this.labels = this.fechasCasos;  //casosPromedioSS.map(v => v.fecha) ;
    const datosSS = casosPromedioSS.map(v => v.casos_15d) as Array<number>;
    const datosSoy = casosPromedioSoy.map(v => v.casos_15d) as Array<number>;
    const datosSM = casosPromedioSM.map(v => v.casos_15d) as Array<number>;
    const datosSA = casosPromedioSA.map(v => v.casos_15d) as Array<number>;
    const datosST = casosPromedioST.map(v => v.casos_15d) as Array<number>;
    const datosCo = casosPromedioCo.map(v => v.casos_15d) as Array<number>;

    fixFechas(datosSS,this.fechasCasos);
    fixFechas(datosSoy,this.fechasCasos);
    fixFechas(datosSM,this.fechasCasos);
    fixFechas(datosSA,this.fechasCasos);
    fixFechas(datosST,this.fechasCasos);
    fixFechas(datosCo,this.fechasCasos);
    
    console.log(casosPromedioSoy,datosSoy,this.fechasCasos);

    console.log(this.dataset[0].data);
    this.dataset = [
      new MyDataset('San Salvador',datosSS,'red'),
      new MyDataset('Soyapango',datosSoy,'yellow'),
      new MyDataset('San Miguel',datosSM,'orange'),
      new MyDataset('Santa Ana',datosSA,'blue'),
      new MyDataset('Santa Tecla',datosST,'cyan'),
      new MyDataset('Colón',datosCo,'green')
    ];
  }

  mounted() : any{
    this.init().catch(err => console.error(err));
  }
}
